import React from 'react'

export const TodoItem = ({todo , onDelete}) => {
    return (
        <div className='mx-5'>
        <h4>{todo.title}</h4>
        <p>{todo.desc}</p>
        <button className='bt btn-sm btn-danger' onClick={ () =>{onDelete(todo)}}>Delete</button>
        </div>
    )
}
